import axios from 'axios';

/**
 *  @summary        
 *  @param
 *  @returns [ {
        "_id":  
        "branchCode":  ,
        "branchName": ,
        "updatedAt":   
    }]
 */
export const getStoreList = () => axios.get('/api/order/branch/incomplete/');

/**
 * @summary      
 * @param branchCode :   
 * @returns [ {
        "_id":  
        "branchCode":  ,
        "branchName": ,
        "irems" [
            "itemCode" :  
            "itemName" :  
            "itemCount" :  
            "itemCost" :  
            "itemVolume" :  
            "itemDepth" :  ?
        ]
        "updatedAt":   
    }]
 */
export const getOrderData = branchCode => axios.get('/api/order/' + branchCode);

/**
 * @summary   
 * @param branchCode :   
 * @returns returnCode
 */
export const updateComplete = branchCode => axios.put('/api/order/complete/' + branchCode);
