import React, { Component } from 'react';
import { RegisterTemplate } from '../../component/template';
import { RegisterWrapper, RegisterTitle, RegisterContent } from '../../component/auth/';
import { Button, InputWithLabel } from '../../component/common';

class UserRegister extends Component {
    constructor() {
        super();
        this.state = {};
    }

    render() {
        return (
            <RegisterTemplate>
                <RegisterWrapper>
                    <RegisterTitle title="  ">
                             .
                    </RegisterTitle>
                    <RegisterContent>
                        <InputWithLabel label=" "
                            type="text"
                            name="custId"
                            placeholder=""
                            autoFocus />
                        <InputWithLabel label=""
                            type="text"
                            name="custPasswd" 
                            placeholder="(8 )" />
                        <InputWithLabel label=" "
                            type="text"
                            name="custPasswdConfirm"
                            placeholder=" " />
                    </RegisterContent>
                    <RegisterContent>
                        <InputWithLabel label=" "
                            type="text"
                            name="custName"
                            placeholder=" "
                            autoFocus />
                        <InputWithLabel label=" "
                            type="text"
                            name="custNumber"
                            placeholder=" "
                            autoFocus />
                        <InputWithLabel label=" "
                            type="text"
                            name="custAddress"
                            placeholder=" "
                            autoFocus />
                        <InputWithLabel label=""
                            type="text"
                            name="companyName"
                            placeholder="   "
                            autoFocus />
                        <InputWithLabel label=" "
                            type="text"
                            name="custEmail"
                            placeholder="     "
                            autoFocus />
                        <InputWithLabel label=" "
                            type="text"
                            name="custPhoneNumber"
                            placeholder=" "
                            autoFocus />
                        <InputWithLabel label=""
                            type="text"
                            name="storName"
                            placeholder=""
                            autoFocus />
                    </RegisterContent>
                    <Button></Button>
                </RegisterWrapper>
            </RegisterTemplate>
        );
    }
}

export default UserRegister;